@import "~bootstrap/scss/bootstrap-grid";

.footer{
  padding: 30px 0;
  background-color: rgb(24, 68, 148);
  color: #fff;
  display: flex;
  flex-direction: row;
  @include media-breakpoint-down(md){
    text-align: center;
  }
}

.footer__info{
  a,span{
    color: rgb(255, 255, 255);
    line-height: 2.167;
    font-size: 12px;
  }
  a{
    display: block;
  }
}

.footer__menu{
  list-style: none;
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  padding: 0;
  text-align: left;
  @include media-breakpoint-down(sm){
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }
}

.footer__menuItem {
  display: inline-block;
  a {
    display: block;
    color: #fff;
    font-size: 14px;
    text-decoration: underline;
    position: relative;
    width: 100%;
  }
  @include media-breakpoint-down(sm){
    margin-bottom: 12px;
    &:last-child{
      margin-bottom: 0;
    }
  }
}

.footer__communicationPanel{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  @include media-breakpoint-down(md){
    justify-content: center;
    flex-direction: column;
    margin-top: 20px;
  }
}

.footer__communicationPanelButton {
  margin-left: 32px;
  border-radius: 3px;
  background-color: #009fe3;
  font-family: 'Montserrat', 'sans-serif';
  color: rgb(255, 255, 255);
  border: none;
  font-weight: bold;
  line-height: 1.2;
  font-size: 14px;
  width: 160px;
  height: 44px;
  cursor: pointer;
  @include media-breakpoint-down(md){
    margin: 20px 0 0;
  }
}

.footer__communicationPanelPhone{
  font-size: 12px;
  font-family: 'Montserrat', 'sans-serif';
  color: rgb(255, 255, 255);
  font-weight: bold;
  text-align: left;
  text-decoration: none;
}
