@import "~bootstrap/scss/bootstrap-grid";

.catalog {
  padding-bottom: 42px;
}

.catalog__path {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 30px 0;

  a {
    font-size: 12px;
    color: rgb(33, 33, 33);
    line-height: 2.333;
    text-decoration: none;

    &:after {
      content: "|";
      display: inline-block;
      margin: 0 13px;
    }

    &:last-child {
      font-weight: bold;

      &:after {
        display: none;
      }
    }
  }
}

.catalog__title {
  font-size: 40px;
  font-family: "Montserrat", sans-serif;
  color: rgb(24, 68, 148);
  font-weight: bold;
  line-height: 1.2;
  margin: 0 0 57px;

  @include media-breakpoint-down(md) {
    font-size: 28px;
  }
}

.catalog__grid {
  display: grid;
  grid-gap: 8px;
  grid-template-columns: 310px 1fr;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "catalog-form catalog-header"
    "catalog-form catalog-main"
    ". catalog-footer";

  @include media-breakpoint-down(md) {
    display: block;

    >* {
      margin-bottom: 20px;
    }
  }
}

.catalog__gridForm {
  grid-area: catalog-form;
}

.catalog__gridHeader {
  grid-area: catalog-header;
}

.catalog__gridMain {
  grid-area: catalog-main;
}

.catalog__gridFooter {
  grid-area: catalog-footer;
}

.catalog__blocks {
  display: grid;
  grid-gap: 8px;
  grid-template-columns: 1fr 1fr 1fr;

  @include media-breakpoint-down(lg) {
    grid-template-columns: 1fr 1fr;
  }

  @include media-breakpoint-down(md) {
    grid-template-columns: 1fr;
  }

}

.catalog__block {
  position: relative;
  text-decoration: none;
  cursor: pointer;

  img {
    width: 100%;
  }
}

.catalog__blockInfo {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(137, 135, 135, 0.85);
  border-radius: 6px;
  transition: 0.15s ease;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 30px;
  color: #fff;

  h3,
  p {
    font-size: 14px;
    line-height: 1.571;
    margin: 0;
  }

  h3 {
    padding-bottom: 33px;
  }
}

.catalog__block:hover {
  .catalog__blockInfo {
    opacity: 1;
  }
}

.catalog__form {
  padding: 22px 18px;
  border-radius: 6px;
  background-color: rgb(245, 245, 245);
  margin-top: -22px;

  * {
    line-height: 1;
  }
}

.catalog__formHeader {
  font-size: 14px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  input {
    background: none;
    border: none;
    text-decoration: underline;
    color: #009fe3;
  }
}

.catalog__formFields {
  margin-top: 19px;
}

.catalog__formFieldsTitle {
  font-size: 10px;
  font-family: "Montserrat", sans-serif;
  color: rgb(159, 163, 167);
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1;
  margin-bottom: 18px;
  display: inline-block;
}

.catalog__formField {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 14px;

  &:last-child {
    margin-bottom: 0;
  }

  input {
    display: none;
  }

  label {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    font-size: 14px;
    color: #212121;
    line-height: 1;
  }

  input+label {
    &:before {
      content: "";
      border: 1px solid rgb(204, 201, 201);
      border-radius: 50%;
      width: 12px;
      height: 12px;
      margin-right: 9px;
      display: block;
    }

    &:after {
      content: "";
      display: block;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background: #009fe3;
      transform: scale(0);
      transition: 0.15s ease;
      position: absolute;
      left: 3px
    }
  }

  input:checked+label {
    &:after {
      transform: scale(1);
    }
  }
}

.catalog__formFieldsMore {
  padding-left: 21px;
  margin-top: 2px;

  button {
    background: none;
    border: none;
    text-decoration: underline;
    font-size: 14px;
    color: #009fe3;
  }
}

.catalog__formButton {
  margin: 20px auto 0;
  padding: 20px 45px;
  background: #009fe3;
  border-radius: 3px;
  font-size: 18px;
  font-family: "Montserrat", sans-serif;
  color: rgb(255, 255, 255);
  font-weight: bold;
  border: none;
  display: block;
}

.catalog__formSort {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  color: rgb(33, 33, 33);

  select {
    border: none;
    background: none;
  }
}

.catalog__pagination {
  display: flex;
  flex-direction: row;
  align-items: center;

  a {
    font-size: 14px;
    color: rgb(33, 33, 33);
    line-height: 2;
    text-decoration: none;
    margin-right: 14px;
    display: inline-block;

    &.active {
      border: 1px solid rgb(204, 201, 201);
      border-radius: 6px;
      padding: 4px 7px;
      margin-right: 7px;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}

.catalog__footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  @include media-breakpoint-down(md) {
    flex-direction: column;
  }
}

.catalog__footerButton {
  font-size: 18px;
  font-family: "Montserrat", sans-serif;
  color: rgb(255, 255, 255);
  border: none;
  border-radius: 3px;
  background: #009fe3;
  line-height: 1;
  padding: 20px 15px;

  @include media-breakpoint-down(md) {
    margin-top: 20px;
  }
}

.catalog__tableLarge {
  display: none;

  @include media-breakpoint-up(lg) {
    display: block;
  }
}

.catalog__tableSmall {
  display: none;

  @include media-breakpoint-down(md) {
    display: block;
  }
}

.catalog__table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 58px;

  th {
    font-size: 10px;
    font-family: "Montserrat", sans-serif;
    color: rgb(159, 163, 167);
    font-weight: bold;
    text-transform: uppercase;
    line-height: 1;
  }

  td {
    font-size: 14px;
    color: rgb(33, 33, 33);
    font-weight: bold;
    line-height: 2;
  }

  a {
    font-size: 14px;
    color: rgb(0, 159, 227);
    display: flex;
    flex-direction: row;
    align-items: center;

    img {
      margin-right: 10px;
    }
  }
}

.catalog__carouselDots {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 35px 0;

  img {
    height: 60px;
    margin-right: 20px;
    cursor: pointer;
    opacity: 0.5;

    &.active {
      opacity: 1;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}

.catalog__shapes {
  display: grid;
  grid-gap: 4px;
  grid-template-columns: repeat(5, 15px);

  img {
    width: 100%;
  }
}

.catalog__featuresTitle {
  font-size: 18px;
  margin: 0 0 30px;
}

.catalog__featuresList {
  margin: 0 0 30px;
  padding: 0;
  font-size: 14px;
  list-style: none;
  border-bottom: 2px solid #f5f5f5;

  li {
    padding-left: 27px;
    color: #212121;
    line-height: 1.333;
    margin-bottom: 14px;

    &:before {
      content: "";
      display: inline-block;
      border-radius: 50%;
      background-color: rgb(0, 159, 227);
      width: 4px;
      height: 4px;
      margin-right: -4px;
      transform: translateX(-27px) translateY(-50%);
    }
  }
}

.catalog__manufacturer {
  display: flex;
  flex-direction: row;

  @include media-breakpoint-down(sm) {
    text-align: center;
    flex-direction: column;
  }
}

.catalog__manufacturerInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  img {
    width: 145px;
    margin-bottom: 35px;
  }

  p {
    margin: 0;
    font-size: 14px;
    color: rgb(33, 33, 33);
    line-height: 1.714;

  }

  @include media-breakpoint-down(sm) {
    img {
      margin: 0 auto;
    }
  }
}

.catalog__manufacturerVideo {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.catalog__manufacturerVideo {
  margin-left: 100px;

  img:first-child {
    width: 239px;
  }

  @include media-breakpoint-down(sm) {
    margin: 30px 0 0;
  }
}

img.catalog__manufacturerVideoPlay {
  position: absolute;
  height: 80px;
}

.catalogDownload {
  position: relative;

  background: {
    image: url(../img/catalog/bg-blurred.png);
    position-x: center;
    position-y: center;
    size: cover;
    repeat: no-repeat;
    attachment: initial;
    origin: initial;
    clip: initial;
    color: #009fe3;
  }

  ;
}

.catalogDownload__wrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 300px;
  justify-content: center;

  img {
    height: 410px;
    margin-right: 100px;
  }

  a {
    text-decoration: none;
    font-size: 18px;
    font-family: "Montserrat", sans-serif;
    color: rgb(255, 255, 255);
    font-weight: bold;
    line-height: 3.178;
    padding: 0 60px;
    display: inline-block;
    border: 2px solid #fff;
    border-radius: 3px;
  }

  @include media-breakpoint-down(md) {
    flex-direction: column;
    height: unset;
    padding: 40px;

    img {
      height: 100%;
      margin: 0 0 20px 0;
    }
  }
}

.catalogSize {
  padding: 120px 0 65px;

  h2 {
    text-align: center;
    margin: 0;
  }

  img {
    margin: 40px auto 0;
    display: block;
    width: 100%;
  }
}

.catalogParameters {
  h2 {
    text-align: center;
    margin: 0 0 30px;
    font-size: 18px;
    color: rgb(33, 33, 33);
    font-weight: bold;
  }

  p {    
    color: rgb(33, 33, 33);


  }

  select {
    display: block;
    width: 100%;
    border: none;
    padding: 10px 0;
    border-bottom: 1px solid #c9c7c7;
    font-size: 10px;
    font-family: "Montserrat", sans-serif;
    color: rgb(159, 163, 167);
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 33px;
  }

  button {
    font-size: 18px;
    font-family: "Montserrat", sans-serif;
    color: rgb(255, 255, 255);
    font-weight: bold;
    line-height: 3.178;
    padding: 0 20px;
    border: none;
    border-radius: 3px;
    background: #009fe3;
  }
}

.catalogSimilar {
  padding: 80px 0 30px;

  h2 {
    font-size: 18px;
    color: rgb(33, 33, 33);
    margin: 0 0 45px;
    text-align: center;
  }
}

.catalogSimilar__carousel {
  position: relative;

  .owl-item {
    >img {
      width: unset !important;
      height: 320px;
      display: block;
      margin: 0 auto;
    }
  }

  .owl-nav {
    position: unset;

    button {
      width: 45px;
      height: 45px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #f5f5f5 !important;
      position: absolute;
      top: 50%;

      &:first-child {
        left: -20px;
        transform: translateX(calc(-100% - 20px)) translateY(-50%);
      }

      &:last-child {
        right: -20px;
        transform: translateX(calc(100% + 20px)) translateY(-50%);
      }

      img {
        height: 13px;
      }

      @include media-breakpoint-down(md) {
        &:first-child {
          left: 20px;
          transform: translateY(-50%);
        }

        &:last-child {
          right: 20px;
          transform: translateY(-50%);
        }
      }
    }
  }
}


.catalogParameters {

  &__twist {
    display: flex;
    justify-content: space-between;

    &__wrapper {
      width: 50%;
      display: flex;
      flex-direction: column;
      transition: 1s;
      .catalogParameters__item:last-child{
        border-bottom: 2px solid #ddd;
      }
      &-right{
        align-items: flex-end;
      }
    }
  }

  

  &__item {
    display: flex;
    flex-direction: column;
    width: 92%;
  }

  &__subtitle {
    position: relative;
    font-family: 'Open Sans';
    font-weight: 700;
    font-size: 14px;
    border-top: 2px solid #ddd;
    padding: 13px 35px 13px 10px;
    display: flex;
    width: 100%;
    margin: 0;
    cursor: pointer;

    &:before {
      content: '';
      position: absolute;
      right: 25px;
      top: 50%;
      border: 5px solid transparent;
      border-top: 5px solid #333;
      margin-top: -3px;
    }
  }

  
  

  .droplist {
    max-height: 40rem;
  }

  &__line {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0 17px 0 0px;
    font-size: 13px;
    p{
      padding: 10px;
      margin: 0;
    }
  }
}
.catalogParameters__btn{
  margin-top: 20px;
}
.catalogParameters__subtitle__inner {
  transition: 0.6s ease-in-out;
  overflow-y: scroll;
  max-height: 0px;
  

  div:nth-child(2n){
    p{
      background: #f5f5f5;
    }
  }
  div:last-child{
    margin-bottom: 10px;
  }
}
.catalogParameters__subtitle__inner::-webkit-scrollbar {
  width: 2px; 
}

.catalogParameters__subtitle__inner::-webkit-scrollbar-thumb {
  background: #ccc;  
}
.theres{
  background: #0fbf30 !important;
  color: #fff !important;
}
.nope{
  background: #fff !important;

 
}
.cher{
  color: #fff !important;
  background: #eb9c3d !important; 
}
.arrow-act:before{
  transform: rotate(180deg);
  top: 40%;
}
.theres , .cher , .nope{
  flex:  0 0 73px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width:767px){
  .catalogParameters__item{
    min-height: 66px
  }

  .catalogParameters__subtitle{
    min-height:66px;
    display: flex;
    align-items: center;
    
  }
  .catalogParameters__line p{
    padding: 7px;
  }
}
@media (max-width:580px){
  .catalogParameters__twist{
    flex-direction: column;
  }
  .catalogParameters__twist__wrapper,
  .catalogParameters__item{
    width: 100%;
  }
  .catalogParameters__twist__wrapper .catalogParameters__item:last-child{
    border-bottom: none;
  }
  .catalogParameters__twist__wrapper-right  .catalogParameters__item:last-child{
    border-bottom: 2px solid #ddd;
  }
}