@import "~bootstrap/scss/bootstrap-grid";

.home {
  h2 {
    font-size: 40px;
    font-family: 'Montserrat', 'sans-serif';

    @include media-breakpoint-down(sm) {
      font-size: 28px;
    }
  }
}

.home__mainTopPanel {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;

  @include media-breakpoint-down(md) {
    padding-top: 40px;
    text-align: center;
  }
}

.home__mainTitle {
  margin: 0;
  padding: 0;
  font-weight: bold;
  font-family: 'Montserrat', 'sans-serif';
  color: #184494;
  font-size: 40px;

  span {
    &:last-child {
      font-weight: normal;
    }
  }

  @include media-breakpoint-down(sm) {
    font-size: 28px;
  }
}

.home__mainTitleUnderline {
  border-bottom: 3px solid #009fe3;
}

.home__getButtons {
  display: flex;
  flex-direction: row;

  @include media-breakpoint-down(md) {
    justify-content: center;
    margin-bottom: 40px;
  }

  @include media-breakpoint-down(sm) {
    flex-direction: column;
    justify-content: center;
    margin-bottom: 40px;
  }
}

.home__btnGetCatalogWrap {
  position: relative;
  margin-top: 161px;

  img {
    position: absolute;
    left: 50%;
    height: 96px;
    bottom: calc(100% + 30px);
  }
}

.home__btnGetCatalog {
  width: 240px;
  height: 60px;
  margin-right: 34px;
  border-radius: 3px;
  font-family: 'Montserrat', 'sans-serif';
  color: rgb(255, 255, 255);
  font-size: 18px;
  background-color: #009fe3;
  border: none;

  @include media-breakpoint-down(sm) {
    margin: 20px auto;
  }
}

.home__btnalculate {
  width: 240px;
  height: 60px;
  margin-top: 161px;
  border-radius: 3px;
  font-family: 'Montserrat', 'sans-serif';
  color: #009fe3;
  font-size: 18px;
  background-color: transparent;
  position: relative;
  border: 2px solid #009fe3;
  margin-right: 34px;

  @include media-breakpoint-down(sm) {
    margin: 20px auto;
  }
}

.home__carousel {
  padding: 0;
  list-style: none;
  width: 680px;
  height: 680px;
  border-radius: 50%;
  background-color: #f5f5f5;
  overflow: hidden;
  position: relative;
  margin: 0 auto;

  .owl-stage-outer,
  .owl-stage,
  .owl-item {
    height: 100%;
  }

  .owl-nav {
    button {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);

      &:first-child {
        left: 24px;
      }

      &:last-child {
        right: 24px;
      }
    }
  }

  @include media-breakpoint-down(lg) {
    width: 400px;
    height: 400px;
  }

  @include media-breakpoint-down(md) {
    width: 450px;
    height: 450px;
  }

  @include media-breakpoint-down(sm) {
    width: 80vw;
    height: 80vw;
  }
}


.home__carouselItem {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  >img {
    width: 325px !important;
    margin-bottom: 33px;
  }

  h3 {
    font-size: 16px;
    color: rgb(135, 135, 134);
    font-weight: bold;
    margin: 0;
  }

  p {
    padding-top: 33px;
    font-size: 14px;
    color: rgb(135, 135, 134);
    margin: 0;
  }

  @include media-breakpoint-down(lg) {
    >img {
      width: 60% !important;
    }

    h3 {
      font-size: 14px;
    }

    p {
      padding-top: 15px;
      font-size: 10px;
    }
  }
}

.home__carouselItemHeader {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.home__carouselItemContainer {
  width: 225px;
  text-align: left;
}

.home__carouselItemShapes {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 4px;

  img {
    width: 14px !important;
  }
}

.home__carouselCustomDots {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 42px;
  margin-top: -42px;
  position: relative;
  z-index: 2;

  @include media-breakpoint-down(md) {
    margin-top: 20px;
  }
}

.home__carouselCustomDotsButton {
  padding: 0;
  margin: 0 8px 0 0;
  opacity: 0.4;
  background-color: transparent;
  border: none;

  &_active {
    opacity: 1;
  }

  &:last-child {
    margin-right: 0;
  }
}

.homeDocumentation {
