.homeAddress {
  overflow: hidden;
  position: relative;
  height: 800px;

  h3 {
    font-family: 'Montserrat', sans-serif;
    font-size: 40px;
    margin: 50px 0 60px;
    color: #009fe3;
  }

  @include media-breakpoint-down(md) {
    height: unset;
  }
}

.homeAddress__quote {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 60px;

  img {
    margin-right: 10px;
  }

  blockquote {
    margin: 0;
    font-size: 17px;
    color: #4c4c4c;
    padding: 23px 15px;

    background: {
      image: url(../img/home/Rectangle.png);
      position-x: center;
      position-y: center;
      repeat: no-repeat;
      size: contain;
    }

    ;

    span {
      display: inline-block;
      font-size: 42px;
      font-family: "Playfair Display", serif;
      color: rgb(0, 159, 227);
      vertical-align: middle;
      transform: translateY(5px);
      margin-right: 4px;
      line-height: 0;
    }
  }
}

.homeAddress__list {
  list-style: none;
  margin: 0 0 100px;
  padding: 0;
}

.homeAddress__listItem {
  font-size: 10px;
  font-family: "Montserrat", sans-serif;
  color: rgb(159, 163, 167);
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  div {
    padding-top: 8px;
    text-transform: none;
    font-size: 18px;
    font-family: "Open Sans", sans-serif;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    address,
    a {
      font-style: normal;
      display: inline-block;
      margin-bottom: 6px;
      text-decoration: none;
      color: rgb(33, 33, 33);

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.homeAddress__socials {
  display: flex;
  flex-direction: row;
  align-items: center;

  a {
    margin-right: 9px;
    display: inline-block;
  }
}

.homeAddress__img {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 45%;

  background: {
    image: url(../img/home/here.png);
    position-x: center;
    position-y: center;
    repeat: no-repeat;
    size: cover;
  }

  ;

  @include media-breakpoint-down(md) {
    display: block;
    position: relative;
    width: 100%;
    left: unset;
    top: unset;
    bottom: unset;
    height: 500px;
    margin-top: 40px;
  }
}

.visiblee .homeIce__brandDesc {
  display: block;
}



// Popup =============================
.mfp-counter {
  display: none;
}

.mfp-with-zoom .mfp-container,
.mfp-with-zoom.mfp-bg {
  opacity: 0;
  -webkit-backface-visibility: hidden;
  /* ideally, transition speed should match zoom duration */
  -webkit-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.mfp-with-zoom.mfp-ready .mfp-container {
  opacity: 1;
}

.mfp-with-zoom.mfp-ready.mfp-bg {
  opacity: 0.8;
}

.mfp-with-zoom.mfp-removing .mfp-container,
.mfp-with-zoom.mfp-removing.mfp-bg {
  opacity: 0;
}

.mfp-arrow {
  opacity: 1 !important;

}

.mfp-arrow-right {
  transform: translateX(475px);
  right: 50%;
}

.mfp-arrow-left {
  transform: translateX(-475px);
  left: 50%;
}

.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
  color: #009fe3;
}

.mfp-close {
  opacity: 1 !important;
}

.mfp-arrow-right:before {
  border-left: 27px solid #2e2e2e;
}

.mfp-arrow-left:before {
  border-right: 27px solid #2e2e2e;
}

.mfp-arrow-right:after {
  border-left: 17px solid #009fe3;
}

.mfp-arrow-left:after {
  border-right: 17px solid #009fe3
}

@media (max-width: 992px) {
  .mfp-arrow-right {
    right: 0;
    transform: none;
  }

  .mfp-arrow-left {
    left: 0;
    transform: none;
  }

  .mfp-wrap {
    width: 95%;
    left: 2%;
  }
}

@media (max-width:500px) {
  .mfp-arrow-right {
    right: -15px;
  }

  .mfp-arrow-left {
    left: -15px;
  }
}


