@import "~bootstrap/scss/bootstrap-grid";

.header {
  background-color: #fff;
  padding: 30px 0;
}

.header__menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;
  @include media-breakpoint-down(md){
    display: none;
  }
}

.header__menuItem {
  a {
    display: block;
    color: #184494;
    font-family: "Montserrat", sans-serif;
    font-size: 14px;
    text-decoration: none;
    position: relative;
    width: 100%;
    text-align: left;
    padding: 16px 0;

    &:after {
      content: "";
      display: none;
      height: 8px;
      width: 23px;
      background-color: #009fe3;
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }

  &_active {
    a {
      &:after {
        display: block;
      }
    }
  }
}

.header__communicationPanel {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  @include media-breakpoint-down(md){
    display: none;
  }
}

.header__communicationPanelSocial {
  margin-left: 18px;
  cursor: pointer;
  img{
    width: 31px;
    height: 31px;
  }
}

.header__communicationPanelButton {
  margin-left: 22px;
  border-radius: 3px;
  background-color: #009fe3;
  font-family: 'Montserrat', 'sans-serif';
  color: rgb(255, 255, 255);
  border: none;
  font-weight: bold;
  line-height: 1.2;
  font-size: 14px;
  width: 160px;
  height: 44px;
  cursor: pointer;
}


.header__communicationPanelPhone {
  font-size: 14px;
  font-family: 'Montserrat', 'sans-serif';
  color: rgb(24, 68, 148);
  font-weight: bold;
  line-height: 1.2;
  text-decoration: none;
}

.header__buttonMenu{
  padding: 0;
  margin: 0;
  border: none;
  background: none;
  float: right;
  position: relative;
  z-index: 10;
  img{
    height: 40px;
  }
  @include media-breakpoint-up(lg){
    display: none;
  }
}



.header__menuMobile{
  position: absolute;
  display: none;
  background: #fff;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9;
  bottom: 0;
  padding: 30px 0;
  &_show{
    display: block;
  }
  h3{
    font-family: "Montserrat", sans-serif;
    font-size: 18px;
  }

  @include media-breakpoint-up(lg){
    display: none;
  }
}

.header__menuMobileNav{
  list-style: none;
  padding: 0 0 30px;
  margin: 0;
}

.header__menuMobileCommunicationPanel{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  padding-top: 30px;
}

.header__menuMobileCommunicationPanelPhone{
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
  color: rgb(24, 68, 148);
  font-weight: bold;
  line-height: 1.2;
  margin-bottom: 20px;
  display: inline-block;
}

.header__menuMobileCommunicationPanelButton{
  border-radius: 3px;
  border: none;
  background: #009fe3;
  padding: 17px 20px;
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
  color: rgb(255, 255, 255);
  font-weight: bold;
  margin-bottom: 20px;
}
